.logs{width:100%;white-space:normal;margin-bottom:14px;table-layout:fixed}.logs th{color:#888}.logs td,.logs th{vertical-align:top;padding:.8em;text-align:left;font-weight:normal;position:relative;word-wrap:break-word;border-bottom:1px solid #eee}.logs th.level.active .log-settings-toggle,.logs th.level:hover .log-settings-toggle{opacity:1}.logs .time{width:180px}.logs .time.relative{width:100px}.logs .app{width:100px;white-space:nowrap;text-overflow:ellipsis;overflow:hidden}@media only screen and (max-width:1100px){.logs td.app,.logs th.app{display:none}}.logs .level{width:100px}.logs .level_2 td.level{border-left:5px solid #FEEFB3}.logs .level_3 td.level{border-left:5px solid #FFBABA}.logs .level_4 td.level{border-left:5px solid #ff8080}.logs div.column{box-sizing:border-box;padding:7px}.logs div.level{font-weight:bold}.logs div.level,.logs div.app,.logs div.time{display:inline-block;width:33%}.logs div.time{padding-right:20px}.logs div.row{position:relative}.logs div.message{border-bottom:1px solid #888}.logs div.level_2{border-left:5px solid #FEEFB3}.logs div.level_3{border-left:5px solid #FFBABA}.logs div.level_4{border-left:5px solid #ff8080}.logs tr.empty td{border-bottom:none}.logs tr.empty td:hover{background-color:white}.logs tr.highlight{background-color:#f8f8f8}.smallHeader,th.level{overflow:visible}.smallHeader>span,th.level>span{margin:-0.8em;padding:.8em;display:block;width:100%;height:100%;cursor:pointer}.smallHeader .log-settings-toggle,th.level .log-settings-toggle{cursor:pointer;display:block;float:right;margin:2px;opacity:.5;transition:opacity .2s}.smallHeader{margin-left:44px;width:62px;padding:10px}.smallHeader .log-settings-toggle:hover{opacity:1}


